<template>
    <div class="qiong-td-fix qiong-pt-10 pl-7 pr-7">
        
        <div class="qiong-txt-td mb-1">
            现在个人支付级别
        </div>

        <div class="br-simple circle-XL qiong-pl-30 qiong-pr-30 pt-3 pb-3 bg-simple">
            <div class="qiong-td">
                <div class="qiong-wide-25 flex-center">
                    <level-image-viewing :bigger="2" 
                        :item="me.vip.named + '_' + me.vip.star"
                        :extra_class="'vip_showing'"
                    ></level-image-viewing>
                </div>
                <div class="qiong-wide-75 fw-b">
                    <table class="w-full qiong-line-hight-15">
                        <tr v-for="(v, i) in result" :key="i">
                            <td>{{ v.txt }}</td>
                            <td class="text-right">{{ v.content }}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>

        <div class="qiong-txt-td mb-1 qiong-pt-10 mt-2">
            到达&nbsp;<div class="d-inline">红宝</div>&nbsp;所需要完成的任务
        </div>

        <div>
            <div class="qiong-td  bg-simple qiong-mb-10 circle-XL pt-1 pb-1" v-for="(v, i) in result" :key="i">
                <div class="qiong-wide-50 qiong-pl-20 pr-0">
                    {{ v.txt }}
                </div>
                <div class="qiong-wide-25 text-right">
                    {{ v.content }}/{{ v.target }}
                </div>
                <div class="qiong-wide-25 qiong-table-fix pr-0">
                    <finised-btn-viewing :rec="Number.parseInt(v.content)" :target="Number.parseInt(v.target)" class=""></finised-btn-viewing>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import QiongAvatarColor from '../../../../components/Qiong/Ui/QiongAvatarColor.vue'
import FinisedBtnViewing from '../../../../components/Viewing/FinisedBtnViewing.vue'
import LevelImageViewing from '../../../../components/Viewing/LevelImageViewing.vue'
    export default {
    components: { QiongAvatarColor, LevelImageViewing,
        FinisedBtnViewing
    },
        props: {
            me: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                result: [

                    {
                        txt: '每周期个人销售活跃点数',
                        content: '100',
                        target: '100', unit: 'PV'
                    },
                    {
                        txt: '每周期团队销售点数',
                        content: '5100',
                        target: '100', unit: 'PV'
                    },
                    {
                        txt: '合资格下线',
                        content: '2',
                        target: '100', unit: ''
                    },
                    {
                        txt: '每周期每合资格下线团队销售点数',
                        content: '2200',
                        target: '100', unit: 'PV'
                    }
                ]
            }
        },
        mounted() {
            
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


